body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #4383c3; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #4383c3 0%,#30b4cb 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}
body, html {
  height: 100%;
  color: #fff !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  display: flex !important;
  justify-content: space-between;
  padding: 10px 30px;
  align-items: center;
}
header a {
  font-size: 24px;
  color: #fff;
  transition: all 0.3s;
}

header a:not(.logo):hover {
  text-decoration: none;
  color: inherit;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.logo img {
  max-width: 170px;
}

.game {
  text-align: center;
}

.score {
  font-size: 4rem;
  font-weight:bold;
  margin: 0 0 30px;
}

.game-board {
  width: 60%;
  margin: 0 auto 50px;
}

.symbol {
  width: 20%;
  background: transparent;
  border: none;
  padding: 12px;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.symbol img {
  display: inline-block;
  width: 50%;
  margin: 0 auto;
}
.symbol span {
  display: block;
  text-align: center;
  color: #fff;
  margin-top: 10px;
}

.symbol:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.symbol:focus {
  outline: none;
}

.choice-info {
  display: flex;
  justify-content: space-around;
}

.hidden {
  display: none !important;
}

.start-btn {
  display: block;
  width: 200px;
  padding: 10px 20px;
  border: none;
  border-radius: 35px;
  background-color: #fff;
  color: #30b4cb;
  font-weight:bold;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s;
  box-shadow: 0px 0px 39px 0px rgba(0,0,0,0.3);
  margin: 30px auto 0;
  text-align: center;
}

.start-btn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  text-decoration: none;
  color: #30b4cb;
}

.how-to-play {
  display: flex;
  flex-direction: column;
  align-items: center;
}

footer {
  padding: 30px;
  /*margin-top: 50px;*/
  text-align: right;
  font-size: 12px;
}

/*Layout*/
#root {
  min-height: 100%;
  position: relative;
  z-index: 1;
}
header {
  height: 150px;
  position: relative;
  z-index: 3;
}
main {
  padding-bottom: 50px;
  position: relative;
  z-index: 2;
}
footer {
  width: 100%;
  height: 50px;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  z-index: 4;
}

/* Preloader */
.lds-ring {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 6px solid #fff;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #fff transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
